/* eslint-disable */
import mock from "../../mock";
import * as RCRTCAdapter from "../assets/js/RCRTCAdapter-1.0.latest.js"
import * as RongCallLib from "../assets/js/RongCallLib.3.2.1.min.js"
// console.log(RCRTCAdapter);
// console.log(RongCallLib);
import * as RongIMLib from '@rongcloud/imlib-v4'
import { installer, RCRTCCode } from '@rongcloud/plugin-rtc'
let im;
let rtcClient;
let rongCallLib;
let message;
let isMute = false;
let inviteUserId;

export const mixin = {
	mounted() {
		console.log("参数：", this.$route.query)
		this.user = mock[this.$route.query.user] || mock.allen;
		console.log("userId:", this.user.userId)

		if (this.user.userId === "allen") {
			this.targetId = "jack"
			inviteUserId = "jack"
		} else {
			this.targetId = "allen"
			inviteUserId = "allen"
		}

		if (this.$route.query.type == 3) {
			this.conversationType = 3;
			this.targetId = "group1"
		}

		this.handelInit()
	},

	methods: {
		handelInit() {
			// 获取 IMLib 实例
			im = RongIMLib.init({ appkey: this.user.appkey })

			// 监听 IMLib 连接状态变化
			im.watch({
				// 连接状态监听
				status(evt) {
					console.log('连接状态码:', evt.status);
				},
				// 消息监听
				message(evt) {
					console.log('收到新消息:', evt.message);
				}
			})



			rtcClient = im.install(installer, { /*初始化参数请参考下方参数说明*/ })
			im.install(RongCallLib.installer);

			var config = {
				timeout: 20000,
				RongIMLib: RongIMLib,
				RongRTC: rtcClient,
				RongRTCAdapter: RCRTCAdapter
			};
			rongCallLib = RongCallLib.init(config);

			im.connect({ token: this.user.token }).then(user => {
				console.log('链接成功, 链接用户 id 为: ', user.id);
			}).catch(error => {
				console.log('链接失败: ', error.code, error.msg);
			});

			// rongCallLib.videoWatch 视频监听，用于接收音视频资源。
			// rongCallLib.commandWatch 消息监听，用于接收通话过程中的消息。


			var videoWatcher = function (result) {
				console.log("视频监听 result", result)
				console.log(result.data)
				var type = result.type;
				var boxEl = document.getElementById('box');
				if (type === 'added') {
					// 添加音视频节点
					var video = result.data;
					console.log(video);
					boxEl.appendChild(video);
				} else if (type === 'removed') {
					// 删除对应音视频节点
					var video = result.data;
					boxEl.removeChild(video);
				} else if (type == 'leave') {
					// 音视频结束, 清空所有音视频 UI
				}
			};
			rongCallLib.videoWatch(videoWatcher);


			var commandWatcher = (msg) => {
				var messageType = msg.messageType;
				// 根据消息类型做对应处理
				console.log("消息监听", messageType, msg)
				message = msg
				console.log(new Date().getSeconds())

			};
			rongCallLib.commandWatch(commandWatcher);
		},

		handelCall(MediaType) {
			var params = {
				conversationType: this.conversationType,
				targetId: this.targetId,
				inviteUserIds: [inviteUserId],
				mediaType: MediaType,
				extra: { test: "extra name" }
			};
			console.log(params)
			rongCallLib.call(params, function (error) {
				if (error) {
					console.error('发起通话失败', error);
				}
				console.log(new Date().getSeconds())
			});

		},

		handelInvite() {
			// 仅 群组 音视频可调用该方法
			var targetId = 'group1';
			var params = {
				conversationType: 3, //RongIMLib.CONVERSATION_TYPE.GROUP,
				targetId: targetId,
				inviteUserIds: ['test'],
				mediaType: RongCallLib.VoIPMediaType.MEDIA_VIDEO
			};
			rongCallLib.invite(params, function (error) {
				if (error) {
					console.error('邀请通话失败', error);
				}
			});
		},

		handelAccept(MediaType) {
			var params = {
				conversationType: message.conversationType,
				targetId: message.targetId,
				mediaType: MediaType
			};
			rongCallLib.accept(params, function (error) {
				if (error) {
					console.error("MediaType:", MediaType, ',接听通话失败', error);
				}
			});
		},

		handelHungup() {
			var params = {
				conversationType: this.conversationType,
				targetId: this.targetId,
			};
			rongCallLib.hungup(params, function (error) {
				if (error) {
					console.error('挂断通话失败', error);
				}
			});
		},

		handelReject() {
			var params = {
				conversationType: this.conversationType,
				targetId: this.targetId,
			};
			rongCallLib.reject(params, function (error) {
				if (error) {
					console.error('拒绝通话失败', error);
				}
			});
		},

		handelSetVideoProfile() {
			//此接口在接通音视频之前做更改分辨率操作
			var callVideoProfile = 40 //代表480p
			RongCallLib.setVideoProfile(callVideoProfile)
		},

		handelVideoToAudio() {
			rongCallLib.videoToAudio();
		},

		handelMute() {
			if (isMute) {
				rongCallLib.unmute();
			} else {
				rongCallLib.mute();
			}
			isMute = !isMute;
		}
	}
}